.profile-info__title {
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
@media (max-width: 600px) {
    .profile-info__title {
        font-size: 27px;
        line-height: 33px;
        padding: 3px 0 0 27px;
    }
}
