.popup-mesto__container-card {
    width: 430px;
    background-color: #FFFFFF;
    border-radius: 10px 10px;
    margin: 0;
    position: relative;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media (max-width: 600px) {
    .popup-mesto__container-card {
        width: 282px;
    }
}
